/* components/ScrollSection/ScrollSection.module.css */
.wrapper {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 5rem 0;
}

.section {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}



.sectionContent {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  will-change: transform;
}

.desktopContent {
  display: none;
}

.mobileContent {
  display: block;
}

.caption {
  font-size: 0.875rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.captionSmall {
  font-size: 0.75rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.chapterTitle {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 1;
  margin-bottom: 3rem;
}

.chapterTitle i {
  font-style: italic;
}

.sectionGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.chapterOne {
  grid-template-areas:
    "image1 image2"
    "label1 label2"
    "image3 image3"
    "label3 label3"
    "image4 image5"
    "label4 label5";
}

.image {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 100%;
  overflow: hidden;
}

.image1 { grid-area: image1; }
.image2 { grid-area: image2; }
.image3 { 
  grid-area: image3; 
  padding-bottom: 50%;
}
.image4 { grid-area: image4; }
.image5 { grid-area: image5; }

.label {
  margin-top: 0.5rem;
  margin-bottom: 2rem;
}

.label1 { grid-area: label1; }
.label2 { grid-area: label2; }
.label3 { grid-area: label3; }
.label4 { grid-area: label4; }
.label5 { grid-area: label5; }

.transitionImage {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

@media (min-width: 768px) {
  .desktopContent {
    display: block;
  }
  
  .mobileContent {
    display: none;
  }

  .sectionGrid {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 2rem;
  }

  .chapterOne {
    grid-template-areas:
      "image1 image2 image3 image3"
      "label1 label2 image3 image3"
      "image4 image4 image5 image5"
      "label4 label4 label5 label5";
  }

  .image3 {
    padding-bottom: 100%;
  }
}